import './Login.css';
import { useState } from 'react';
import { BASE_URL } from '../Common';
import { useNavigate } from "react-router-dom";
import RecoverUsernameModal from './RecoverUsernameModal';
import RecoverPasswordModal from './RecoverPasswordModal';

function Login() {
    const navigate = useNavigate();

    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');

    const [showsRecoverUsernameModal, setShowsRecoverUsernameModal] = useState(false);
    const [showsRecoverPasswordModal, setShowsRecoverPasswordModal] = useState(false);

    const handleKeyDown = (event) => {
        if (event.key === 'Enter') {
            toggleLogin(event);
        }
    };

    const toggleLogin = async (event) => {
        event.preventDefault();
        try {
            const response = await fetch(BASE_URL + '/users/auths/login', {
                method: 'POST',
                headers: {
                  'Content-Type': 'application/json'
                },
                body: JSON.stringify({ username, password })
            });
    
            const data = await response.json();
            const statusCode = response.status;
            if (statusCode == 200) {
                localStorage.setItem('accessToken', data.accessToken);
                localStorage.setItem('refreshToken', data.refreshToken.refreshToken);
                fetchUserProfile()
            } else {
                alert(" .");
            }
        } catch (error) {
            alert(" .");
        }
    };

    const fetchUserProfile = async () => {
        try {
            const token = localStorage.getItem('accessToken');
            const response = await fetch(BASE_URL + '/users/profiles', {
                method: 'GET',
                headers: {
                    'Request-Type': 'CURRENT',
                    'Authorization': `Bearer ${token}`
                }
            });
    
            const data = await response.json();
            const statusCode = response.status;
            if (statusCode == 200) {
                localStorage.setItem("user", JSON.stringify(data.userProfile));
                alert(" .");
                if (data.userProfile.permissions[0] == "ADMIN" || data.userProfile.permissions[0] == "ROOT_ADMIN") {
                    navigate("/statics");
                } else {
                    navigate("/");
                }
            } else {
                alert(" .");
            }
        } catch (error) {
            alert(" .");
        }
    }

    return (
        <div className='login'>
            <div className='login-box'>
                <a className='login-title-kor semi-bold'> </a>
                <a className='login-title-eng semi-bold'>PORTAL LOGIN</a>

                <form className='login-input-box-list'>
                    <div className='login-input-box'>
                        <img src="/img/user.png" className="login-input-icon" alt="logo" />
                        <input type='text' className='login-input' placeholder=''
                        onChange={(event) => setUsername(event.target.value)}
                        onKeyDown={handleKeyDown}
                        />
                    </div>
                    <div className='login-input-box'>
                        <img src="/img/password.png" className="login-input-icon" alt="logo" />
                        <input type='password' className='login-input' placeholder=''
                        onChange={(event) => setPassword(event.target.value)}
                        onKeyDown={handleKeyDown}
                        />
                    </div>

                    <div className='login-button cursor-pointer' onClick={toggleLogin}>
                        <a className='login-button-text semi-bold'></a>
                    </div>
                </form>

                <div className='find-box'>
                    <a className='find-text semi-bold cursor-pointer' onClick={() => { setShowsRecoverUsernameModal(true) }}> </a>
                    <a className='find-text semi-bold cursor-pointer'> | </a>
                    <a className='find-text semi-bold cursor-pointer' onClick={() => { setShowsRecoverPasswordModal(true) }}> </a>
                </div>

                <a className='bottom-title-text semi-bold'>   </a>
            </div>

            {showsRecoverUsernameModal && <RecoverUsernameModal closeModal={() => { setShowsRecoverUsernameModal(false) }} />}
            {showsRecoverPasswordModal && <RecoverPasswordModal closeModal={() => { setShowsRecoverPasswordModal(false) }} />}
        </div>
    );
}

export default Login;